@use "../foundation/variables" as *;
@use "../foundation/mixins" as *;

// lazyblockエディタ表示
.lazyblock {
    padding: 0;
    box-shadow: 0 0 0 1px violet;

    .lzb-content-title {
        display: none;
    }
}

// / lightbox

.c-gallery-wrapper {
    display: grid;
    grid-template-columns: repeat(3, 1fr) !important;
    column-gap: min(calc((30/1000)* 100vw), 30px) !important;
    row-gap: min(calc((50/1000)* 100vw), 50px) !important;

    @include respond(phone) {
        grid-template-columns: repeat(2, 1fr) !important;
    }
}

.c-gallery {
    width: 100%;
    height: auto;
    aspect-ratio: 1/1;
    position: relative;

    img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    &__text {
        position: absolute;
        bottom: 0;
        width: 100%;
        padding-block: calc(9px + var(--lending-trim));
        padding-inline: 11px;
        line-height: 1.5;
        font-size: rem(17);
        background-color: rgba(0, 0, 0, 0.4);
        color: $color-white;
    }

    @include respond(phone) {
        &__text {
            font-size: rem(14);
            padding-inline: 0.5em;
        }
    }

}

